<template>
    <div class="answer">
        <div class="left-content" ref="content">
            <div style="position: absolute; right:20px; margin:15px 5px;">
                <el-button @click="openDrawer" v-if="!drawer" type="primary" icon="el-icon-back" circle></el-button>
            </div>
            <el-card class="box-card" style="margin:68px auto 108px auto; max-width:900px; padding:50px;">
                <div class="block" v-for="(item, index) in qsitem" :key="index">
                    <div v-if="item.type === 'radio'">
                        <p>{{index+1}}. {{item.title}}</p>
                        <el-radio-group v-model="answer[index]">
                            <el-radio :label="option.id" v-for="(option, i) in item.options" :key="i" @change="refreshProgress(index)">{{option.value}}</el-radio>
                        </el-radio-group>
                    </div>
                    <div v-else-if="item.type === 'checkbox'">
                        <p>{{index+1}}. {{item.title}}</p>
                        <el-checkbox-group v-model="answer[index]">
                            <el-checkbox :label="option.id" v-for="(option, i) in item.options" :key="i" @change="refreshProgress(index)">{{option.value}}</el-checkbox>
                        </el-checkbox-group>
                    </div>
                    <div v-else-if="item.type === 'textarea'">
                        <p>{{index+1}}. {{item.title}}</p>
                        <el-input type="textarea" :autosize="{ minRows: 3, maxRows: 5}" v-model="answer[index]" @blur="refreshProgress(index)"></el-input>
                    </div>
                    <div v-else-if="item.type === 'rate'">
                        <p>{{index+1}}. {{item.title}}</p>
                        <el-rate v-model="answer[index]" @change="refreshProgress(index)"></el-rate>
                    </div>
                    <el-divider></el-divider>
                </div>
                <div class="block" style="text-align:center;">
                    <el-button type="primary" @click="showData">提交问卷</el-button>
                </div>
            </el-card>
            <div class="answer-line" ref="line">
                <div class="answer-line-info">{{answerNumber}}题已回答(共{{qsNum}}个问题)</div>
                <div class="answer-line-anime">
                    <el-progress :show-text="false" :stroke-width="10" :percentage="percentage" color="#fff"></el-progress>
                </div>
            </div>
        </div>
        <div class="right-info">
            <el-button @click="closeDrawer" type="primary" icon="el-icon-right" circle style="float:right; margin: 0 10px;"></el-button>
            <div style="padding: 20px">
                <p>{{title}}</p>
                <p>{{introduction}}</p>
                <p>问题数</p><p>{{qsNum}}</p>
                <p>完成所需时间</p><p>{{doTime}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import QS from 'qs';

export default {
    data() {
        return {
            drawer: true,
            answer: {},
            markIds: [],
            answerNumber: 0,
            percentage: 0,

            qsitem: [],
            title: '',
            introduction: '',
            qsNum: 0,
            doTime: ''
        }
    },

    methods: {
        openDrawer() {
            this.drawer = true;
            this.$refs.content.style.width = "calc(100% - 450px)";
            this.$refs.line.style.width = "calc(100% - 450px)";
        },

        closeDrawer() {
            this.drawer = false;
            this.$refs.content.style.width = "100%";
            this.$refs.line.style.width = "100%";
        },

        // 输出测试数据
        showData() {
            window.console.log(this.answer);QS.stringify(1)
            axios.post('http://localhost/answer', this.answer)
                .then((res) => {
                    window.console.log(res.data);
                })
        },

        // 向服务器请求问卷信息
        getQsItem() {
            axios.get('http://localhost/qs')
                .then((res) => {
                    const qs = res.data;
                    const qsitem = res.data.qsitem;

                    this.title = qs.title;
                    this.introduction = qs.introduction;
                    this.qsNum = qs.qsNum;
                    this.doTime = qs.doTime;
                    for(let key in qsitem) { // 将请求的数据赋给data
                        this.$set(this.qsitem, key, qsitem[key]);
                    }

                    for(let key in this.qsitem) { // 初始化answer的数据类型
                        if(this.qsitem[key].type === 'textarea') {
                            this.$set(this.answer, key, '');
                        }
                        else if(this.qsitem[key].type === 'rate') {
                            this.$set(this.answer, key, null);
                        }
                        else {
                            this.$set(this.answer, key, []);
                        }
                    }

                    // 初始化markIds的数据类型
                    for (const key in this.qsitem) {
                        this.markIds[key] = 0;
                    }
            })
        },

        // 更新答题进度
        refreshProgress(id) {
            this.markIds[id] = 1;
            if(this.answer[id].length == 0) { this.markIds[id] = 0; }
            
            let answerNumber = 0;
            for (const key in this.markIds) {
                if(this.markIds[key] == 1) { answerNumber++; }
            }

            this.answerNumber = answerNumber;
            this.percentage = this.answerNumber/this.qsNum*100;
        }
    },

    mounted() {
        this.getQsItem();
    }
}
</script>

<style scoped>
.answer {
    width: 100%;
    height: 100%;
    background: #fff;
    overflow: hidden;
}

.left-content {
    width: calc(100% - 450px);
    height: 100%;
    background: #fefefe;
    float: left;
    overflow-y: scroll;
    transition: .5s;
    margin-right: 25px;
}

.right-info {
    margin: 15px 15px;
}

.answer-line {
    width: calc(100% - 466.8px);
    min-height: 40px;
    background: #409EFF;
    position: fixed;
    bottom: 0;
    color: #fff;
    line-height: 40px;
    text-align: center;
    transition: .5s;
    z-index: 999;
}

.answer-line-info {
    display: inline-block;
    margin: 0px 20px;
}

.answer-line-anime {
    width: 150px;
    display: inline-block;
}
</style>